img {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-card {
  box-shadow: 0 5px 5px -4px black;
}

.info-item {
  margin: 8px;
  justify-content: center;
  text-align: center;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 24px;
  background-color: rgb(92, 92, 247);
}

.button,
.load-more {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.container_load-more {
  margin-bottom: 15px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
  max-width: calc(100vw - 80px);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.gallery li {
  height: 450px;
  overflow: hidden;
}

.gallery img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.gallery a {
  text-decoration: none;
  color: #2a2a2a;
}

.info {
  display: flex;
  justify-content: space-around;

  text-align: center;
  padding: 0 5px;
  font-size: 15px;
}

.is-hidden {
  display: none;
}
